import useGlobalStore from '@/store/common/global'
import { MsafeTransactionSubType } from '@/types'
import useClmmSDKStore from '@cetus/stores/src/useClmmSDKStore'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import { d, fixDown } from '@cetus/utils'

type GetRemoveParams = {
  posId: string
  poolAddress: string
  coinTypeA: string
  coinTypeB: string
  amountA: string
  amountB: string
  liquidity: string
  lowerTick: number
  upperTick: number
  posType: 'clmm' | 'farms'
  rewarderCoinTypes: any
  farmsPoolId?: string
  farmsPosId?: string
}

export default function usePosRemove() {
  const { clmmSdk } = useClmmSDKStore()
  const { peripherySdk } = usePeripherySDKStore()
  const { slippage } = useGlobalStore()

  const getRemoveTsPayload = async (params: GetRemoveParams) => {
    const {
      posId,
      poolAddress,
      coinTypeA,
      coinTypeB,
      amountA,
      amountB,
      liquidity,
      lowerTick,
      upperTick,
      posType,
      rewarderCoinTypes,
      farmsPoolId,
      farmsPosId
    } = params
    const minAmountA = d(amountA).mul(d(1).sub(d(slippage)))
    const minAmountB = d(amountB.toString()).mul(d(1).sub(d(slippage)))
    console.log('🚀 ~ getRemoveTsPayload ~ minAmountA:', minAmountA, minAmountB)

    if (posType === 'clmm') {
      const parameter = {
        coinTypeA,
        coinTypeB,
        pool_id: poolAddress,
        pos_id: posId,
        delta_liquidity: liquidity,
        min_amount_a: fixDown(minAmountA.toString(), 0) || '',
        min_amount_b: fixDown(minAmountB.toString(), 0) || '',
        collect_fee: true,
        rewarder_coin_types: rewarderCoinTypes
      }
      console.log('🚀 ~ getRemoveTsPayload ~ parameter:', parameter)

      const tx = await clmmSdk.Position.removeLiquidityTransactionPayload(parameter)

      const msafeParams = {
        action: MsafeTransactionSubType.DecreaseLiquidity,
        txbParams: parameter
      }
      console.log('🚀 ~ getRemoveTsPayload ~ msafeParams:', tx, msafeParams)
      return {
        tx,
        msafeParams
      }
    } else {
      const parameter = {
        pool_id: farmsPoolId || '',
        coinTypeA,
        coinTypeB,
        position_nft_id: farmsPosId || '',
        clmm_pool_id: poolAddress,
        min_amount_a: fixDown(minAmountA.toString(), 0) || '',
        min_amount_b: fixDown(minAmountB.toString(), 0) || '',
        collect_rewarder: false,
        clmm_rewarder_types: rewarderCoinTypes,
        delta_liquidity: liquidity,
        unstake: false,
        close_position: false
      }
      console.log('🚀 ~ getRemoveTsPayload ~ parameter:', parameter)

      const tx = await peripherySdk.Farms.removeLiquidityPayload(parameter)

      const msafeParams = {
        action: 'FarmingDecreaseLiquidity',
        txbParams: parameter
      }
      console.log('🚀 ~ getRemoveTsPayload ~ msafeParams:', msafeParams, tx)
      return {
        tx,
        msafeParams
      }
    }
  }

  // 移除并关闭仓位
  const getCloseTsPayload = async (params: GetRemoveParams) => {
    const {
      posId,
      poolAddress,
      coinTypeA,
      coinTypeB,
      amountA,
      amountB,
      liquidity,
      lowerTick,
      upperTick,
      posType,
      rewarderCoinTypes,
      farmsPoolId,
      farmsPosId
    } = params

    const minAmountA = d(amountA).mul(d(1).sub(d(slippage)))
    const minAmountB = d(amountB.toString()).mul(d(1).sub(d(slippage)))
    console.log('🚀 ~ getCloseTsPayload ~ minAmountA:', params, minAmountA.toString(), minAmountB.toString())
    console.log('🚀 ~ getCloseTsPayload ~ parameter.rewarderCoinTypes:', rewarderCoinTypes)

    if (posType === 'clmm') {
      const parameter = {
        coinTypeA,
        coinTypeB,
        pool_id: poolAddress,
        pos_id: posId || '',
        min_amount_a: fixDown(minAmountA.toString(), 0) || '',
        min_amount_b: fixDown(minAmountB.toString(), 0) || '',
        rewarder_coin_types: rewarderCoinTypes,
        collect_fee: false
      }
      console.log('🚀 ~ getCloseTsPayload ~ parameter:', parameter)

      const tx = await clmmSdk.Position.closePositionTransactionPayload(parameter)

      const msafeParams = {
        action: MsafeTransactionSubType.RemoveLiquidity,
        txbParams: parameter
      }
      console.log('🚀 ~ getCloseTsPayload ~ msafeParams:', tx, msafeParams)
      return {
        tx,
        msafeParams
      }
    } else {
      const parameter = {
        pool_id: farmsPoolId || '',
        coinTypeA,
        coinTypeB,
        position_nft_id: farmsPosId || '',
        clmm_pool_id: poolAddress,
        min_amount_a: fixDown(minAmountA.toString(), 0) || '',
        min_amount_b: fixDown(minAmountB.toString(), 0) || '',
        collect_fee: true,
        collect_rewarder: true,
        clmm_rewarder_types: rewarderCoinTypes,
        delta_liquidity: liquidity,
        unstake: true,
        close_position: true,
        clmm_position_id: posId
      }
      console.log('🚀 ~ getCloseTsPayload ~ parameter:', parameter)
      const tx = await peripherySdk.Farms.removeLiquidityPayload(parameter)

      const msafeParams = {
        action: MsafeTransactionSubType.FarmingRemoveLiquidity,
        txbParams: parameter
      }
      console.log('🚀 ~ getCloseTsPayload ~ msafeParams:', msafeParams, tx)
      return {
        tx,
        msafeParams
      }
    }
  }
  return {
    getRemoveTsPayload,
    getCloseTsPayload
  }
}
